@import 'reset';
@import 'global';
@import 'fonts';




*,
*::after,
*::before {
  box-sizing: border-box;
  color: #FFF;
  font-family: Gotham Pro;
  font-style: normal;
  font-weight: 400;
  line-height: 98.2%;
  letter-spacing: -0.48px;
}





.wrapper {}

.header {
  &__inner {
    display: flex;
    justify-content: space-between;
  }

  .nav {
    margin-top: 39px;
    display: flex;
    gap: 27px;

    &__link {
      letter-spacing: -0.02em;
      font-size: 14px;
      font-weight: 300;
    }
  }

  .logo {
    margin-top: 34px;
    font-size: 24px;
  }

  .application {
    margin-top: 34px;



    font-size: 24px;
    letter-spacing: -0.48px;
    text-decoration-line: underline;
  }
}

.container {
  height: 100%;
}


.main {

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    max-height: 1117px;

    max-width: 1728px;
    margin: 0 auto;
    padding: 0 40px;
    /*     position: relative; */
  }

  &__box {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
  }

  &__title {
    font-size: 160px;
    letter-spacing: -9.6px;
    text-transform: uppercase;
    text-align: center;
    width: auto;
    position: relative;
  }

  &__logo {
    content: '';
    position: absolute;
    bottom: 72%;
    left: 27%;
    /*     width: 199px;
    height: 199.774px; */
    width: 12.07%;
    z-index: 2;

    min-width: 80px;

    img {
      width: 100%;
    }
  }

  &__signature {
    content: '';
    position: absolute;
    top: 0;
    left: 8.5%;

    width: 15.898%;
    z-index: 2;

    min-width: 150px;
    transform: translateY(-100%);

    img {
      width: 100%;
    }
  }

  &__background {
    position: absolute;
    top: 47%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 695px;
    mix-blend-mode: difference;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__btn {
    display: block;
    width: 165px;
    padding: 20px 30px;
    margin: 34px auto 0 auto;
    border-radius: 100px;
    background: #FFF;
    position: static;
    font-size: 14px;
    font-weight: 300;
    color: #000;
    z-index: 2;
    position: relative;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  &__range {
    display: flex;
    align-items: end;
    gap: 20px;
  }

  &__range-link {
    font-size: 14px;
    letter-spacing: -0.28px;
    text-transform: capitalize;
    line-height: 98.2%;
    /* 13.748px */

    display: block;
    max-height: 54px;
    padding: 20px 30px;
    align-items: center;

    border-radius: 100px;
    border: 1px solid #FFF;
  }

  &__text {
    font-size: 16px;
    line-height: 133.5%;
    font-weight: 300;
    /* 21.36px */
    width: 489px;
  }
}

@media(max-height: 800px) {
  .main__background {
    max-width: 440px;
  }

  .main__title {
    font-size: 110px;
  }

  .main__logo {
    max-width: 120px;
  }

  .main__signature {
    max-width: 120px;
  }
}

@media(max-width: 1479px) {
  .main__title {
    font-size: 135px;
  }

  .main__background {
    width: 580px;
  }
}

@media(max-width: 1260px) {

  .main__title {
    font-size: 110px;
  }

  .main__background {
    width: 515px;
  }
}


@media(max-width: 1000px) {
  .main__info {
    /* justify-content: space-between;  есть при неограниченном размера окна браузера*/
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .main__text {
    text-align: center;
  }

  .main__background {
    width: 450px;
  }

  .main__logo {
    bottom: 65%;
    left: 23%;
  }

  .main__title {
    font-size: 89px;
  }
}

/* Верстка меню бургера */

.header__body {
  display: none;
  /* отключение меню бургера при большой ширине */
}

@media(max-width: 1005px) {

  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;

    /*       &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: blueviolet;
        z-index: 2;
      } */

    &__inner {
      display: none;
      /*         отключение обычного меню */
    }

    &__body {
      position: relative;

      display: flex;
      justify-content: flex-end;
      height: 80px;
      align-items: center;
    }

    &__logo {
      flex: 0 0 60px;
      border-radius: 50%;
      overflow: hidden;
      z-index: 3;

      img {
        max-width: 100%;
        display: block;
      }
    }

    /*       &__burger {
        display: none;
      } */




    &__list {
      display: flex;
      position: relative;
      z-index: 2;
    }

    &__link {
      text-transform: uppercase;
      font-size: 18px;
      margin-right: 10px;
    }
  }


  .header__body {
    height: 40px;
  }

  .header__logo {
    flex: 0 0 40px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    position: relative;
    z-index: 3;

    span {
      background-color: #fff;
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      bottom: 50%;
      transform: translateY(50%);
      transition: all 0.3s ease-in-out 0s;
    }
  }

  .header__burger::before,
  .header__burger::after {
    content: '';
    background-color: #fff;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    transition: all 0.3s ease-in-out 0s;
  }

  .header__burger::before {
    bottom: 0;

  }

  .header__burger::after {
    top: 0;
  }

  .header__burger.active::before {
    transform: rotate(45deg) translateY(50%);
    bottom: 50%;

  }

  .header__burger.active::after {
    transform: rotate(-45deg) translateY(-50%);
    top: 50%;

  }

  .header__burger.active {
    span {
      transform: scale(0);
    }
  }

  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: auto;
    background-color: rgba(46, 48, 34, 0.694);
    overflow: auto;
    padding: 20px 10px 20px 10px;
    transition: all 0.3s ease-in-out 0s;
  }

  .header__menu.active {
    top: 0;
  }

  .header__list {
    display: block;

    li {
      margin: 0 0 20px 0;
    }
  }
}



/* адаптация */
@media (max-width: 800px) {
  .main__background {
    width: 350px;
  }

  .main__title {
    font-size: 89px;
    z-index: 2;
    font-size: 89px;
    z-index: 2;
    color: #ffffff;
    -webkit-text-stroke: #000 1px;
  }
}


@media (max-width: 500px) {
  .main__text {
    width: auto;
  }

  .main__inner {
    padding: 0 20px;
  }

  .main__title {
    font-size: 69px;
    z-index: 2;

  }
}


