*,
*::after,
*::before {
  box-sizing: border-box;
  color: #FFF;
  font-family: Gotham Pro;
  font-style: normal;
  font-weight: 400;
  line-height: 98.2%;
  letter-spacing: -0.48px;
}

.header__inner {
  display: flex;
  justify-content: space-between;
}
.header .nav {
  margin-top: 39px;
  display: flex;
  gap: 27px;
}
.header .nav__link {
  letter-spacing: -0.02em;
  font-size: 14px;
  font-weight: 300;
}
.header .logo {
  margin-top: 34px;
  font-size: 24px;
}
.header .application {
  margin-top: 34px;
  font-size: 24px;
  letter-spacing: -0.48px;
  text-decoration-line: underline;
}

.container {
  height: 100%;
}

.main__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  max-height: 1117px;
  max-width: 1728px;
  margin: 0 auto;
  padding: 0 40px;
  /*     position: relative; */
}
.main__box {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.main__title {
  font-size: 160px;
  letter-spacing: -9.6px;
  text-transform: uppercase;
  text-align: center;
  width: auto;
  position: relative;
}
.main__logo {
  content: "";
  position: absolute;
  bottom: 72%;
  left: 27%;
  /*     width: 199px;
  height: 199.774px; */
  width: 12.07%;
  z-index: 2;
  min-width: 80px;
}
.main__logo img {
  width: 100%;
}
.main__signature {
  content: "";
  position: absolute;
  top: 0;
  left: 8.5%;
  width: 15.898%;
  z-index: 2;
  min-width: 150px;
  transform: translateY(-100%);
}
.main__signature img {
  width: 100%;
}
.main__background {
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 695px;
  mix-blend-mode: difference;
}
.main__background img {
  width: 100%;
  height: 100%;
}
.main__btn {
  display: block;
  width: 165px;
  padding: 20px 30px;
  margin: 34px auto 0 auto;
  border-radius: 100px;
  background: #FFF;
  position: static;
  font-size: 14px;
  font-weight: 300;
  color: #000;
  z-index: 2;
  position: relative;
}
.main__info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}
.main__range {
  display: flex;
  align-items: end;
  gap: 20px;
}
.main__range-link {
  font-size: 14px;
  letter-spacing: -0.28px;
  text-transform: capitalize;
  line-height: 98.2%;
  /* 13.748px */
  display: block;
  max-height: 54px;
  padding: 20px 30px;
  align-items: center;
  border-radius: 100px;
  border: 1px solid #FFF;
}
.main__text {
  font-size: 16px;
  line-height: 133.5%;
  font-weight: 300;
  /* 21.36px */
  width: 489px;
}

