}
@media (max-width: 1000px) {
  .main__info {
    /* justify-content: space-between;  есть при неограниченном размера окна браузера*/
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .main__text {
    text-align: center;
  }

  .main__background {
    width: 450px;
  }

  .main__logo {
    bottom: 65%;
    left: 23%;
  }

  .main__title {
    font-size: 89px;
  }
}
/* Верстка меню бургера */
.header__body {
  display: none;
  /* отключение меню бургера при большой ширине */
}

@media (max-width: 1005px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 50;
    /*       &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: blueviolet;
        z-index: 2;
      } */
    /*       &__burger {
        display: none;
      } */
  }
  .header__inner {
    display: none;
    /*         отключение обычного меню */
  }
  .header__body {
    position: relative;
    display: flex;
    justify-content: flex-end;
    height: 80px;
    align-items: center;
  }
  .header__logo {
    flex: 0 0 60px;
    border-radius: 50%;
    overflow: hidden;
    z-index: 3;
  }
  .header__logo img {
    max-width: 100%;
    display: block;
  }
  .header__list {
    display: flex;
    position: relative;
    z-index: 2;
  }
  .header__link {
    text-transform: uppercase;
    font-size: 18px;
    margin-right: 10px;
  }

  .header__body {
    height: 40px;
  }

  .header__logo {
    flex: 0 0 40px;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    position: relative;
    z-index: 3;
  }
  .header__burger span {
    background-color: #fff;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    bottom: 50%;
    transform: translateY(50%);
    transition: all 0.3s ease-in-out 0s;
  }

  .header__burger::before,
.header__burger::after {
    content: "";
    background-color: #fff;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    transition: all 0.3s ease-in-out 0s;
  }

  .header__burger::before {
    bottom: 0;
  }

  .header__burger::after {
    top: 0;
  }

  .header__burger.active::before {
    transform: rotate(45deg) translateY(50%);
    bottom: 50%;
  }

  .header__burger.active::after {
    transform: rotate(-45deg) translateY(-50%);
    top: 50%;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: auto;
    background-color: rgba(46, 48, 34, 0.694);
    overflow: auto;
    padding: 20px 10px 20px 10px;
    transition: all 0.3s ease-in-out 0s;
  }

  .header__menu.active {
    top: 0;
  }

  .header__list {
    display: block;
  }
  .header__list li {
    margin: 0 0 20px 0;
  }
}
